import { Button, Divider, List, ListItem, ListItemAvatar, ListItemText, Typography } from "@material-ui/core";
import React from "react";
import { checkIfEmpty } from "../../../../helpers/common.helper";

export default function GeneratedList(props) {
  const { generatedList } = props;
  return (
    !checkIfEmpty(generatedList) && (
      <div>
        <Typography style={{ fontSize: "16px", marginTop: "14px" }}>
          List
        </Typography>
        <List dense={true} style={{ margin: "-8px -16px" }}>
          {generatedList.map((obj, index) => (
            <div key={index}>
              <Divider variant="fullWidth" component="li" />
              <ListItem alignItems="flex-start">
                <ListItemAvatar style={{ minWidth: "44px" }}>
                  <span>{obj.date}</span>
                </ListItemAvatar>
                <ListItemText
                  style={{ paddingLeft: "10px" }}
                  primary={
                    <React.Fragment>
                      <Typography>{obj.user.name}</Typography>
                    </React.Fragment>
                  }
                />
              </ListItem>
            </div>
          ))}
        </List>
        <Button variant="contained" color="primary">
          Save
          <i
            style={{ marginLeft: "10px" }}
            className="fas fa-save"
            aria-hidden="true"
          ></i>
        </Button>
      </div>
    )
  );
}
