import ExpansionPanel from "@material-ui/core/ExpansionPanel";
import ExpansionPanelSummary from "@material-ui/core/ExpansionPanelSummary";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import React, { Component } from "react";
import { isPopularVisibile } from "../../../helpers/visibility.helper";
import PermissionDenied from "../../common/PermissionDenied";
import "./../Food.css";
import PopularTable from "./PopularTable";

export default class Popular extends Component {
  render() {
    const { classes, popularFoodData, profileObj } = this.props;
    return (
      <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel3a-content"
          id="panel3a-header"
        >
          <Typography className={classes.heading}>Popular</Typography>
        </ExpansionPanelSummary>
        {isPopularVisibile(profileObj) ? (
          <PopularTable
            style={{ paddingLeft: "5px" }}
            rows={popularFoodData}
            {...this.props}
          />
        ) : (
          <PermissionDenied profileObj={profileObj}/>
        )}
      </ExpansionPanel>
    );
  }
}
