import { Button, TextField, Typography } from "@material-ui/core";
import { Component, default as React } from "react";
import { connect } from "react-redux";
import { postData } from "../../actions/action";
import { ADD_FEEDBACK_URL, createUrl } from "../../constants/url.constants";
import FeedbackModel from "../../models/FeedbackModel";

class Feedback extends Component {
  constructor(props) {
    super(props);
    this.state = { feedback: "" };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange($event) {
    this.setState({ feedback: $event.target.value });
  }

  sendFeedback = () => {
    const { feedback } = this.state;
    const { profileObj, sendFeedback } = this.props;
    if (feedback && feedback.length > 3) {
      let feedbackObj = new FeedbackModel(
        profileObj,
        feedback,
        new Date()
      );
      sendFeedback(feedbackObj);
      this.setState({ feedback: "" });
    }
  };

  render() {
    const { feedback } = this.state;
    return (
      <div>
        <Typography paragraph>Feedback</Typography>
        <TextField
          id="standard-multiline-static"
          label="Give Us Feedback"
          multiline
          rows="6"
          placeholder="Let's make it better together!"
          style={{ width: "100%" }}
          margin="normal"
          onChange={this.handleChange}
          value={feedback}
        />
        <Button variant="contained" color="primary" onClick={this.sendFeedback}>
          Send
          <i
            style={{ marginLeft: "10px" }}
            className="fa fa-paper-plane"
            aria-hidden="true"
          ></i>
        </Button>
      </div>
    );
  }
}

Feedback.propTypes = {};

function mapStateToProps(state, ownProps) {
  return {};
}

function mapDispatchToProps(dispatch) {
  return {
    sendFeedback: feedbackObj =>
      dispatch(postData(createUrl(ADD_FEEDBACK_URL), feedbackObj))
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Feedback);
