import {
  ExpansionPanel,
  ExpansionPanelSummary,
  Typography
} from "@material-ui/core";
import Fab from "@material-ui/core/Fab";
import AddIcon from "@material-ui/icons/Add";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import React, { Component } from "react";
import { connect } from "react-redux";
import { getData } from "../../../actions/action";
import { continuousCall } from "../../../constants/time.constants";
import { FETCH_APPROVED_USERS } from "../../../constants/types.constants";
import {
  createUrl,
  GET_APPROVED_USERS_URL
} from "../../../constants/url.constants";
import {
  getFirstDayofTheMonth,
  getLastDayofTheMonth
} from "../../../helpers/date.helper";
import { findUserByName } from "../../../helpers/user.helper";
import IntegrationAutosuggest from "./auto-suggest/IntegrationAutosuggest";
import CleaningList from "./list/CleaningList";

class CleaningAdmin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      list: [],
      userName1: "",
      userName2: "",
      firstDate: getFirstDayofTheMonth(),
      lastDate: getLastDayofTheMonth()
    };
    this.updateUser = this.updateUser.bind(this);
    this.updateDate = this.updateDate.bind(this);
  }

  addToList() {
    const { userName1, userName2 } = this.state;
    let { list } = this.state;
    const { approvedUsers } = this.props;
    if (userName1) {
      const user1 = findUserByName(approvedUsers, userName1);
      const user2 = findUserByName(approvedUsers, userName2);
      list.push({ user1, user2 });
      this.setState({ ...this.state, list });
    }
  }

  updateUser(userName, value) {
    this.setState({ ...this.state, [userName]: value });
  }

  updateDate(date, value) {
    this.setState({ ...this.state, [date]: value });
  }

  fetchAllCleaningData(props) {
    const { fetchApprovedUsers } = props;
    fetchApprovedUsers();
  }

  componentDidMount() {
    this.cleaningInterval = continuousCall(
      this.fetchAllCleaningData,
      this.props
    );
  }

  render() {
    const { list, userName1, userName2, firstDate, lastDate } = this.state;
    const { approvedUsers, classes } = this.props;
    return (
      <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>Cleaning</Typography>
        </ExpansionPanelSummary>
        <div style={{ padding: "8px 16px" }}>
          <IntegrationAutosuggest
            suggestions={approvedUsers}
            label="User 1"
            placeholder="Search a User"
            updateUser={this.updateUser}
            userName="userName1"
          />
          <IntegrationAutosuggest
            suggestions={approvedUsers}
            label="User 2"
            placeholder="Search a User"
            updateUser={this.updateUser}
            userName="userName2"
          />
          <Fab
            style={{ marginTop: "14px" }}
            size="small"
            color="primary"
            aria-label="add"
          >
            <AddIcon onClick={() => this.addToList()} />
          </Fab>
          <CleaningList
            list={list}
            firstDate={firstDate}
            lastDate={lastDate}
            updateDate={this.updateDate}
            userName1={userName1}
            userName2={userName2}
          />
        </div>
      </ExpansionPanel>
    );
  }

  componentWillUnmount() {
    clearInterval(this.cleaningInterval);
  }
}

CleaningAdmin.propTypes = {};

function mapStateToProps(state, ownProps) {
  return {
    approvedUsers: state.adminReducer.approvedUsers
  };
}

function mapDispatchToProps(dispatch) {
  return {
    fetchApprovedUsers: () =>
      dispatch(getData(createUrl(GET_APPROVED_USERS_URL), FETCH_APPROVED_USERS))
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CleaningAdmin);
