import Button from "@material-ui/core/Button";
import ExpansionPanel from "@material-ui/core/ExpansionPanel";
import ExpansionPanelDetails from "@material-ui/core/ExpansionPanelDetails";
import ExpansionPanelSummary from "@material-ui/core/ExpansionPanelSummary";
import TextField from "@material-ui/core/TextField";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import React, { Component } from "react";
import { connect } from "react-redux";
import { postData } from "../../../actions/action";
import { ADD_RECO_URL, createUrl } from "../../../constants/url.constants";
import RecommendationModel from "../../../models/RecommendationModel";
import "./../Food.css";

export class Recommendation extends Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.state = { name: "" };
    this.addRecommendation = this.addRecommendation.bind(this);
  }

  addRecommendation() {
    const { name } = this.state;
    const { profileObj, addRecommendation, fetchAllFoodData } = this.props;
    if (name && name.length > 3) {
      let recommendation = new RecommendationModel(
        profileObj.email,
        name,
        new Date()
      );
      addRecommendation(recommendation);
      this.setState({ name: "" });
      setTimeout(() => {
        fetchAllFoodData(this.props, this.state);
      }, 1000);
    }
  }

  handleChange($event) {
    this.setState({ name: $event.target.value });
  }

  render() {
    const { name } = this.state;
    const { classes } = this.props;
    return (
      <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>Recommendation</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails style={{ display: "block" }}>
          <Typography variant="caption" className="details-header">
            Please check if already present!
          </Typography>
          <TextField
            id="standard-name"
            label="Food Name"
            placeholder="Min. 3 characters"
            className={classes.textField}
            value={name}
            onChange={this.handleChange}
            margin="normal"
          />
          <Button
            variant="contained"
            color="primary"
            onClick={this.addRecommendation}
          >
            Send
            <i
              style={{ marginLeft: "10px" }}
              className="fa fa-paper-plane"
              aria-hidden="true"
            ></i>
          </Button>
        </ExpansionPanelDetails>
      </ExpansionPanel>
    );
  }
}

Recommendation.propTypes = {};

function mapStateToProps(state, ownProps) {
  return {};
}

function mapDispatchToProps(dispatch) {
  return {
    addRecommendation: recommendation =>
      dispatch(postData(createUrl(ADD_RECO_URL), recommendation))
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Recommendation);
