import Icon from "@material-ui/core/Icon";
import Paper from "@material-ui/core/Paper";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import React from "react";
import {
  DOWNVOTE,
  UPVOTE,
  VOTE_POINTS_KEY
} from "../../../constants/vote.constants";
import CircularIndeterminate from "../../common/CircularIndeterminate";

const useStyles = makeStyles(theme => ({
  root: {
    width: "100%"
  },
  paper: {
    width: "100%",
    overflowX: "auto"
  }
}));

export default function VoteTable(props) {
  const classes = useStyles();
  const { rows, vote } = props;

  return (
    <div className={classes.root}>
      <Paper className={classes.paper}>
        {rows && rows.length ? (
          <Table className={classes.table} size="small">
            <TableHead>
              <TableRow>
                <TableCell>Recommendations</TableCell>
                <TableCell>Vote</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map(row => (
                <TableRow key={row.id}>
                  <TableCell component="th" scope="row">
                    {row.foodName}
                  </TableCell>
                  <TableCell align="right">
                    <Icon onClick={() => vote(row.id, UPVOTE)}>
                      <i
                        style={{
                          color: row[VOTE_POINTS_KEY] === 1 ? "green" : "grey"
                        }}
                        className="fas fa-thumbs-up"
                      ></i>
                    </Icon>{" "}
                    <Icon onClick={() => vote(row.id, DOWNVOTE)}>
                      <i
                        style={{
                          color: row[VOTE_POINTS_KEY] === -0.5 ? "red" : "grey"
                        }}
                        className="fas fa-thumbs-down"
                      ></i>
                    </Icon>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        ) : (
          <CircularIndeterminate />
        )}
      </Paper>
    </div>
  );
}
