import {
  POPULAR_POINTS_KEY,
  VOTE_POINTS_KEY
} from "../constants/vote.constants";
import { checkIfEmpty } from "./common.helper";

export const mergeRecommendationsAndVotes = (recommendationData, voteData) => {
  let foodData = [];
  let voteDataIsEmpty = checkIfEmpty(voteData);
  if (!checkIfEmpty(recommendationData)) {
    Object.keys(recommendationData).forEach(key => {
      let recommendation = recommendationData[key];
      if (!voteDataIsEmpty && voteData[key]) {
        recommendation[VOTE_POINTS_KEY] = voteData[key].points;
      }
      foodData.push(recommendation);
    });
  }
  return foodData;
};

export const voteFoodData = (recommendationID, foodData, vote) => {
  let newFoodData = [];
  foodData.forEach(food => {
    if (food.id === recommendationID) {
      food[VOTE_POINTS_KEY] = vote;
    }
    newFoodData.push(food);
  });
  return newFoodData;
};

export const generatePopularFoodData = (recommendationData, voteData) => {
  let popularFoodData = [];
  let voteDataIsEmpty = checkIfEmpty(voteData);
  if (!checkIfEmpty(recommendationData)) {
    Object.keys(recommendationData).forEach(key => {
      let recommendation = recommendationData[key];
      recommendation[POPULAR_POINTS_KEY] = 0;
      if (!voteDataIsEmpty && voteData[key]) {
        recommendation[POPULAR_POINTS_KEY] = voteData[key];
      }
      popularFoodData.push(recommendation);
    });
  }
  return popularFoodData.sort(compareFoodByPoints);
};

/* comparator to order objects based on points in reverse order */
function compareFoodByPoints(obj1, obj2) {
  let comparison = 0;
  if (obj1.points > obj2.points) comparison = 1;
  if (obj2.points > obj1.points) comparison = -1;

  return comparison * -1; // -1 is to reverse sort
}
