export function getWeek() {
  let curr = new Date();
  curr.setHours(0, 0, 0, 0);

  let week = [];
  for (let i = 1; i <= 7; i++) {
    let first = curr.getDate() - curr.getDay() + i;
    let day = new Date(curr.setDate(first));
    week.push(day);
  }
  return week;
}

export function getLastWeek() {
  let currWeek = {};

  let curr = new Date();
  curr.setHours(0, 0, 0, 0);

  let date = curr.getDate() - curr.getDay() - 6;
  let day = new Date(curr.setDate(date));
  currWeek.startDate = day.toLocaleDateString();

  date = curr.getDate() - curr.getDay() + 7;
  day = new Date(curr.setDate(date));
  currWeek.endDate = day.toLocaleDateString();

  return currWeek;
}

export function getCurrentWeek() {
  let nextWeek = {};

  let curr = new Date();
  curr.setHours(0, 0, 0, 0);

  let date = curr.getDate() - curr.getDay() + 1;
  let day = new Date(curr.setDate(date));
  nextWeek.startDate = day.toLocaleDateString();

  date = curr.getDate() - curr.getDay() + 7;
  day = new Date(curr.setDate(date));
  nextWeek.endDate = day.toLocaleDateString();

  return nextWeek;
}

export function getLastWeekStartDate() {
  let curr = new Date();
  curr.setHours(0, 0, 0, 0);

  let date = curr.getDate() - curr.getDay() - 6;
  let startDay = new Date(curr.setDate(date));
  return startDay;
}

export function getFirstDayofTheMonth() {
  let startDate = new Date();
  startDate.setDate(1);
  return startDate;
}
export function getLastDayofTheMonth() {
  let today = new Date();
  return new Date(today.getFullYear(), today.getMonth() + 1, 0);
}

export function addDaysToDate(curr, days) {
  let oldDate = new Date(curr.valueOf());
  let date = oldDate.getDate() + days;
  let newDate = new Date(oldDate.setDate(date));
  return newDate.toLocaleDateString();
}
