import { FETCH_ALL_VOTES, FETCH_VOTES_BY_EMAIL } from "../constants/types.constants";
import { initialState } from "./app.reducer";

const voteReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_VOTES_BY_EMAIL:
      return {
        ...state,
        voteDataByEmail: action.data
      };
    case FETCH_ALL_VOTES:
      return {
        ...state,
        voteData: action.data
      };
    default:
      return state;
  }
};

export default voteReducer;
