import React from "react";

export function FoodIcon(props) {
  return (
    <i style={{ color: props.color }} className="fas fa-utensils icons"></i>
  );
}

export function GroceryIcon(props) {
  return (
    <i
      style={{ color: props.color }}
      className="fas fa-shopping-basket icons"
    ></i>
  );
}

export function CleaningIcon(props) {
  return <i style={{ color: props.color }} className="fas fa-broom icons"></i>;
}

export function ScheduleIcon(props) {
  return (
    <i style={{ color: props.color }} className="fas fa-calendar-alt icons"></i>
  );
}

export function AdminIcon(props) {
  return (
    <i style={{ color: props.color }} className="fas fa-user-cog icons"></i>
  );
}

export function FeedbackIcon(props) {
  return (
    <i style={{ color: props.color }} className="fas fa-comments icons"></i>
  );
}

export function AboutUsIcon(props) {
  return (
    <i style={{ color: props.color }} className="fas fa-address-card icons"></i>
  );
}
