import { FETCH_USER } from "../constants/types.constants";
import { initialState } from "./app.reducer";

const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_USER:
      return {
        ...state,
        profileObj: action.data
      };
    default:
      return state;
  }
};

export default userReducer;
