import {
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Typography
} from "@material-ui/core";
import React, { Component } from "react";
import { connect } from "react-redux";
import { postData } from "../../actions/action";
import { continuousCall } from "../../constants/time.constants";
import {
  FETCH_ALL_RECOS,
  FETCH_ALL_VOTES,
  FETCH_VOTES_BY_EMAIL
} from "../../constants/types.constants";
import {
  createUrl,
  GET_ALL_RECOS_URL,
  GET_ALL_VOTES_BY_EMAIL_URL,
  GET_ALL_VOTES_URL,
  POST_VOTE_URL
} from "../../constants/url.constants";
import { ACCESS_FOOD_KEY } from "../../constants/user.constants";
import {
  generatePopularFoodData,
  mergeRecommendationsAndVotes,
  voteFoodData
} from "../../helpers/food.helper";
import { isAccessible } from "../../helpers/visibility.helper";
import VoteModel from "../../models/VoteModel";
import PermissionDenied from "../common/PermissionDenied";
import SimpleExpansionPanel from "./SimpleExpansionPanel";

class Food extends Component {
  constructor(props) {
    super(props);
    this.state = {
      foodData: {},
      popularFoodData: {},
      week: props.currentWeek
    };
    this.vote = this.vote.bind(this);
    this.fetchAllFoodData = this.fetchAllFoodData.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  vote(recommendationID, points) {
    const { foodData } = this.state;
    const { profileObj, addVote } = this.props;
    this.setState({
      foodData: voteFoodData(recommendationID, foodData, points)
    });
    let vote = new VoteModel(
      recommendationID,
      profileObj.email,
      points,
      new Date()
    );
    addVote(vote);
  }

  handleChange(event) {
    this.setState({
      ...this.state,
      week: event.target.value
    });
  }

  fetchAllFoodData(props, state) {
    const {
      fetchRecommendations,
      fetchVotes,
      fetchVotesByEmail,
      profileObj
    } = props;
    const { week } = state;
    fetchRecommendations(new Date(week.startDate), new Date(week.endDate));
    fetchVotes(new Date(week.startDate), new Date(week.endDate));
    fetchVotesByEmail(
      profileObj.email,
      new Date(week.startDate),
      new Date(week.endDate)
    );
  }

  componentDidMount() {
    clearInterval(this.foodInterval);
    this.foodInterval = continuousCall(
      this.fetchAllFoodData,
      this.props,
      this.state
    );
  }

  componentDidUpdate(prevProps, prevState) {
    const { recommendationData, voteDataByEmail, voteData } = this.props;
    const { week } = this.state;
    if (week !== prevState.week) {
      this.componentDidMount();
    }
    if (
      recommendationData !== prevProps.recommendationData ||
      voteDataByEmail !== prevProps.voteDataByEmail ||
      voteData !== prevProps.voteData
    ) {
      this.setState({
        foodData: mergeRecommendationsAndVotes(
          recommendationData,
          voteDataByEmail
        ),
        popularFoodData: generatePopularFoodData(recommendationData, voteData)
      });
    }
  }

  render() {
    const { foodData, popularFoodData, week } = this.state;
    const { profileObj, currentWeek, lastWeek } = this.props;
    return isAccessible(profileObj, ACCESS_FOOD_KEY) ? (
      <div>
        <Typography paragraph>Food</Typography>
        <FormControl style={{ marginBottom: "16px" }}>
          <InputLabel htmlFor="date-simple">Week</InputLabel>
          <Select
            value={week}
            onChange={this.handleChange}
            inputProps={{
              name: "date",
              id: "date-simple"
            }}
          >
            <MenuItem value={currentWeek}>
              This Week
              {/* {currentWeek.startDate} - {currentWeek.endDate} */}
            </MenuItem>
            <MenuItem value={lastWeek}>
              Previous Week
              {/* {lastWeek.startDate} - {lastWeek.endDate} */}
            </MenuItem>
          </Select>
        </FormControl>
        <SimpleExpansionPanel
          foodData={foodData}
          popularFoodData={popularFoodData}
          vote={this.vote}
          fetchAllFoodData={this.fetchAllFoodData}
          {...this.props}
        />
      </div>
    ) : (
      <PermissionDenied profileObj={profileObj} />
    );
  }

  componentWillUnmount() {
    clearInterval(this.foodInterval);
  }
}

Food.propTypes = {};

function mapStateToProps(state, ownProps) {
  return {
    recommendationData: state.recommendationReducer.recommendationData,
    voteData: state.voteReducer.voteData,
    voteDataByEmail: state.voteReducer.voteDataByEmail
  };
}

function mapDispatchToProps(dispatch) {
  return {
    fetchRecommendations: (weekStartDate, weekEndDate) =>
      dispatch(
        postData(
          createUrl(GET_ALL_RECOS_URL),
          { weekStartDate, weekEndDate },
          FETCH_ALL_RECOS
        )
      ),
    fetchVotes: (weekStartDate, weekEndDate) =>
      dispatch(
        postData(
          createUrl(GET_ALL_VOTES_URL),
          { weekStartDate, weekEndDate },
          FETCH_ALL_VOTES
        )
      ),
    fetchVotesByEmail: (email, weekStartDate, weekEndDate) =>
      dispatch(
        postData(
          createUrl(GET_ALL_VOTES_BY_EMAIL_URL),
          { email, week: { weekStartDate, weekEndDate } },
          FETCH_VOTES_BY_EMAIL
        )
      ),
    addVote: vote => dispatch(postData(createUrl(POST_VOTE_URL), vote))
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Food);
