import { Button, Divider, List, ListItem, ListItemAvatar, ListItemText, Typography } from "@material-ui/core";
import React from "react";
import { checkIfEmpty } from "../../../../helpers/common.helper";
import { addDaysToDate } from "../../../../helpers/date.helper";
import MaterialUIPickers from "../date-picker/MaterialUIPickers";
import GeneratedList from "./GeneratedList";

let generatedList = [];

const generateList = (list, firstDate, lastDate) => {
  generatedList = [];
  let start = firstDate.getDate();
  let end = lastDate.getDate();
  for (let index = start; index < end + 1; index++) {
    if (list[(index - 1) % list.length].user1.email)
      generatedList.push({
        date: addDaysToDate(firstDate, index - 1),
        user: list[(index - 1) % list.length].user1
      });
    if (list[(index - 1) % list.length].user2.email)
      generatedList.push({
        date: addDaysToDate(firstDate, index - 1),
        user: list[(index - 1) % list.length].user2
      });
  }
};

export default function CleaningList(props) {
  const { list, firstDate, lastDate, updateDate } = props;
  return (
    !checkIfEmpty(list) && (
      <div>
        <Typography style={{ fontSize: "16px", marginTop: "14px" }}>
          List
        </Typography>
        <List dense={true} style={{ margin: "-8px -16px" }}>
          {list.map((obj, index) => (
            <div key={index}>
              <Divider variant="fullWidth" component="li" />
              <ListItem alignItems="flex-start">
                <ListItemAvatar style={{ minWidth: "44px" }}>
                  <span>Day {index + 1}</span>
                </ListItemAvatar>
                <ListItemText
                  style={{ paddingLeft: "10px" }}
                  primary={
                    <React.Fragment>
                      <Typography>{obj.user1.name}</Typography>
                      <Typography>{obj.user2.name}</Typography>
                    </React.Fragment>
                  }
                />
              </ListItem>
            </div>
          ))}
        </List>
        <MaterialUIPickers
          updateDate={updateDate}
          label="From Date"
          defaultDate={firstDate}
          type="firstDate"
        />
        <MaterialUIPickers
          type="lastDate"
          label="To Date"
          updateDate={updateDate}
          defaultDate={lastDate}
        />
        <Button
          variant="contained"
          color="primary"
          onClick={() => generateList(list, firstDate, lastDate)}
        >
          Generate
          <i
            style={{ marginLeft: "10px" }}
            className="fas fa-tools"
            aria-hidden="true"
          ></i>
        </Button>
        <GeneratedList generatedList={generatedList} {...props} />
      </div>
    )
  );
}
